<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Attendances</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span> Employee Attendance </span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>

        <!-- compamy settings starts -->
    <teleport to="#breadcrumb">
         <ul class="navbar-nav flex-row ms-auto">
                <li class="nav-item more-dropdown">
                    <div class="dropdown custom-dropdown-icon">
                        <a href="javascript:;" class="nav-link dropdown-toggle" id="ddlSettings" data-bs-toggle="dropdown" aria-expanded="false">
                            <span>Select a company</span>
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                viewBox="0 0 24 24"
                                fill="none"
                                stroke="currentColor"
                                stroke-width="2"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                class="feather feather-chevron-down"
                            >
                                <polyline points="6 9 12 15 18 9"></polyline>
                            </svg>
                        </a>
                        <ul class="dropdown-menu dropdown-menu-end" aria-labelledby="ddlSettings">
                            <li @click="getData(5)"><a class="dropdown-item" data-value="Mail" href="javascript:void(0);">BUSHMAN SAFARI</a></li>
                            <li @click="getData(6)"><a class="dropdown-item" data-value="Settings" href="javascript:void(0);">COPEX CONTRACTORS</a></li>
                        </ul>
                    </div>
                </li>
            </ul>
    </teleport>

 <h3 class="text-center">
  {{ company_id == 5 ? 'BUSHMAN SAFARI TRACKERS ATTTENDANCE' : company_id == 6 ? 'COPEX CONTRACTORS ATTENDANCE' : null }}
</h3>

        <div class="d-flex justify-content-end align-items-center mb-3">
            <div class="d-flex align-items-center gap-2 me-2">
                <input 
                    type="date" 
                    v-model="selectedDate" 
                    @change="getEmployeeAttendances"
                    class="form-control form-control-sm" 
                    style="width: 150px;"
                />
            </div>
            <button @click="printAttendance()" class="btn btn-success">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
                    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
                    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
                </svg>
            </button>
        </div> 
        
              <!-- loading spinner starts -->
        <div v-if="loading_spinner" v-html="tableShimmer()"></div>
        <!-- Loading spinner ends -->
        <div v-else class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="attendances" :columns="columns" :options="table_option">
                            <template #status="props">
                                <span 
                                    class="badge" 
                                    :class="{
                                        'bg-success': props.row.status === 'present',
                                        'bg-danger': props.row.status === 'absent'
                                    }"
                                >
                                    {{ props.row.status }}
                                </span>
                            </template>
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>



    </div>
</template>
<script setup>
    import { onMounted, ref, inject, computed } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import globalSearchField from '@/components/reusables/globalSearchField.vue';
    import { globalSearch } from '@/components/reusables/globalSearch';
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    useMeta({ title: 'Employee Attendance' });  
    const company_id = ref('');
    const showError = inject('showError');
    const formatNumber = inject('formatNumber');
    const tableShimmer = inject('tableShimmer');
    const printPendingGrnItemsReport = inject('printPendingGrnItemsReport');    
    
    const loading_spinner = ref(false);
    const columns = ref(['SNo', 'name', 'status', 'check_in_time']);    
    const attendances = ref([]);
    const filter = ref('')
    const selectedDate = ref(new Date().toISOString().split('T')[0]);
    
    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 30, 50, 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo', 'name', 'status', 'check_in_time'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const getEmployeeAttendances = async () => {
        loading_spinner.value = true;
        try {
            const response = await axiosInstance.get(`head-office-attendances?date=${selectedDate.value}&company_id=${company_id.value}`);
            attendances.value = response.data.map((item, index) => ({
                SNo: index + 1,
                name: item.name,
                status: item.status,
                check_in_time: item.check_in_time || '--'
            }));
        } catch (error) {
          console.log(error)
            showError(error.message);
        }finally{
            loading_spinner.value = false;
        }
    };

    const printAttendance = async () => {
  try {
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    document.body.appendChild(iframe);

    const companyName = company_id.value == 5 ? 'BUSHMAN SAFARI TRACKERS' : company_id.value == 6 ? 'COPEX CONTRACTORS CO.LTD' : 'COMPANY';

    const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>Employee Attendance Report</title>
<style>
  body { font-family: 'Courier New', monospace; margin: 40px; font-size: 13px; }
  .text-style { font-family: 'Courier New', monospace; }
  .company-title { text-align: center; }
  .title { text-align: center; font-size: 20px; margin: 10px 0; }
  table { width: 100%; border-collapse: collapse; margin-top: 10px; page-break-inside: auto; }
  thead { display: table-header-group; }
  tfoot { display: table-footer-group; }
  tr { page-break-inside: avoid; page-break-after: auto; }
  th, td { border: 0.5px solid #000; padding: 6px; }
  th { background-color: #f8f8f8; font-weight: bold; }
  .text-right { text-align: right; }
  .text-center { text-align: center; }
  hr { border: 0; border-top: 0.5px solid #000; margin: 0px 0; }
  .badge { padding: 4px 8px; border-radius: 4px; font-weight: bold; }
  .badge-present { background-color: #28a745; color: white; }
  .badge-absent { background-color: #dc3545; color: white; }
  .summary-row { page-break-inside: avoid; }
  @media print { 
    body { margin: 10mm; } 
    table { page-break-inside: auto; }
    tr { page-break-inside: avoid; page-break-after: auto; }
    thead { display: table-header-group; }
    tfoot { display: table-footer-group; }
  }
</style>
</head>
<body>

<div class="company-title text-style">
  <i>
  <h1>${companyName}</h1>
  <div><b>Address:</b> Msamvu, P.O.BOX 678, Morogoro</div>
  <div><b>Tel:</b> 0713 000 777, 0657 111 555</div>
  <div><b>Email:</b> info@copexcontractors.co.tz</div>
  <div><b>TIN:</b> 135-489-247 &nbsp;&nbsp; <b>VRN:</b> 40-032826C</div>
  </i>
</div>

<hr style="margin-top:20px">

<div class="title text-style">
  EMPLOYEE ATTENDANCE REPORT
</div>

<div class="text-center text-style">
  <b>Date:</b> ${selectedDate.value}
</div>

<hr>

<table>
  <thead>
    <tr>
      <th>No.</th>
      <th>Employee Name</th>
      <th class="text-center">Status</th>
      <th class="text-center">Check-in Time</th>
    </tr>
  </thead>
  <tbody>
    ${attendances.value.map((attendance, index) => `
    <tr>
      <td>${index + 1}</td>
      <td>${attendance.name || 'N/A'}</td>
      <td class="text-center">
        <span class="badge ${attendance.status === 'present' ? 'badge-present' : 'badge-absent'}">
          ${attendance.status || 'N/A'}
        </span>
      </td>
      <td class="text-center">${attendance.check_in_time || '--'}</td>
    </tr>
    `).join('')}
  </tbody>
  <tfoot>
    <tr class="summary-row">
      <td colspan="4" class="text-right">
        <strong>Total Employees: ${attendances.value.length}</strong> | 
        <strong>Present: ${attendances.value.filter(a => a.status === 'present').length}</strong> | 
        <strong>Absent: ${attendances.value.filter(a => a.status === 'absent').length}</strong>
      </td>
    </tr>
  </tfoot>
</table>

</body>
</html>
`;

    const iframeDoc = iframe.contentWindow.document;
    iframeDoc.open();
    iframeDoc.write(printContent);
    iframeDoc.close();

    iframe.onload = () => {
      iframe.contentWindow.focus();
      iframe.contentWindow.print();
      setTimeout(() => {
        document.body.removeChild(iframe);
      }, 1000);
    };
  } catch (error) {
    showError(`Failed to print attendance report: ${error.message}`);
  }
};
    
    const getData = (id) => {
         company_id.value = id; 
         getEmployeeAttendances() 
        }    

  onMounted(async () => {
    // // Fetch employee attendances on mount
    // await getEmployeeAttendances();
  });
</script>
